import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/portfolio',
      name: 'portfolio',
      component: () => import('./views/Portfolio.vue'),
    },
    {
      path: '/contacts',
      name: 'contacts',
      component: () => import('./views/Contacts.vue'),
    },
  ],
});
